<script>
    import {fade} from 'svelte/transition';
    import {firebase, db} from './../firebase.js';
    import {FirebaseApp, User, Doc, Collection, StorageRef} from 'sveltefire';

    let getUser;
    let newPassword, newPhoneNumber;
    let alertBox, alertBoxContent;

    firebase.auth().onAuthStateChanged(function(user) {
        if (user) {
            getUser = user;
        } else {
            // No user is signed in.
        }
    });

    function setNewEmail(email) {

        let user = firebase.auth().currentUser;

        user.updateEmail(email).then(function() {

            alertBoxCall('Эл. почта обновлена, нажмите кнопку подтвердить.')

        }).catch(function(error) {
            alertBoxCall('Сессия устарела, перезайдите в аккаунт и повторите попытку.')
        });
    }

    function setNewPassword() {

        let user = firebase.auth().currentUser;

        user.updatePassword(newPassword).then(function() {

            alertBoxCall('Пароль обновлен.')

        }).catch(function(error) {
            alertBoxCall('Сессия устарела, перезайдите в аккаунт и повторите попытку.')
        });
    }

    // ALERT BOX
    function alertBoxCall(mes) {

        if (!alertBox) {

            alertBoxContent = mes;
            alertBox = true;
        }
        setTimeout(()=>{
            alertBox = false;
        }, 5000)
    }

    // Cloud Functions
    let updateUserPhoneNumber = firebase.functions().httpsCallable('updateUserPhoneNumber');

    // Call cloud functions
    function updatePhoneNumber(newPhoneNumber) {
        
        if (newPhoneNumber.length === 12) {

            updateUserPhoneNumber({newPhoneNumber})
            .then(function(result) {
                alertBoxCall('Новый номер сохранен.');
                console.log('updated', result)
            }).catch(function(error) {
                console.log(error.code, error.message, error.details)
            });
        } else {
            alertBoxCall('Введите новый номер в формате "+71234567890"');
        }
    }
</script>

<div class="p-2 min-h-screen bg-gray-200">
    <FirebaseApp {firebase}>
        <User let:user>

            <div class="mx-auto p-2 bg-white rounded-lg shadow-md lg:w-2/3">

                <!-- User info -->
                <div class="mt-2">
                    Отображаемое имя: 
                    <input type="text" value={user.displayName} 
                    on:input={(e)=> user.updateProfile({displayName: e.target.value}).then(console.log('updated'))}>
                </div>
                <div class="mt-2">
                    <p>
                        Номер телефона: 
                        <input type="text" value={user.phoneNumber} 
                        on:input={(e)=> newPhoneNumber = e.target.value}>

                        <button class="btn-orange" on:click|once={()=> updatePhoneNumber(newPhoneNumber)}>Изменить</button>
                    </p>
                </div>
                <div class="mt-2">
                    Эл. почта: 
                    <input type="text" value={user.email} 
                    on:change={(e)=> setNewEmail(e.target.value)}>
                    <button class="btn-orange">Изменить</button>
                    {#if !user.emailVerified}
                        <button class="btn-orange"
                        on:click={()=> user.sendEmailVerification().then(alertBoxCall('Письмо отправлено, проверьте почту.'))}>
                        Подтвердить
                        </button>
                    {:else}
                        <img src="./icons/check.svg" alt="confirmed" class=" inline-block">
                    {/if}
                </div>
                <div class="mt-2">
                    Новый пароль:
                    <input type="text" placeholder="Новый пароль" bind:value={newPassword}>
                    <button class="btn-orange" on:click={setNewPassword}>Изменить</button>
                </div>

                <hr>

                <!-- Account info -->
                <Doc path={`users/${user.uid}`} let:data={acc}>

                    <div class="mt-4">
                        Баланс:
                        {acc.balance} руб.
                    </div>
                    <div class="mt-2">
                        Промокод:
                        {acc.username}
                    </div>
                </Doc>
            </div>
        </User>
    </FirebaseApp>
</div>

<!-- ALERT BOX -->
{#if alertBox}
    <div id="alertBox" class=" fixed top-0">

        <div transition:fade="{{ duration: 500 }}" class="p-3 border bg-white rounded-lg shadow-lg">

            <p class="text-center">
                {alertBoxContent}
            </p>

            <p class="mt-2 text-center">
                <button class="btn-orange" on:click={() => alertBox = false}>Ок</button>
            </p>

        </div>

    </div>
{/if}

<style>input {
  border-width: 1px;
}

